.InputRange{
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 1.5em;

    .range-slider {
        width: 90%; 
    }

    .rs-range {
        margin-top: 5px;
        width: 100%;
        -webkit-appearance: none;
        &:focus {
            outline: none;
        }
        &::-webkit-slider-runnable-track {
            width: 100%;
            height: 1px;
            cursor: pointer;
            box-shadow: none;
            background: black;
            border-radius: 0px;
            border: 0px solid #010101;
        }
        &::-moz-range-track {
            width: 100%;
            height: 1px;
            cursor: pointer;
            box-shadow: none;
            background: black;
            border-radius: 0px;
            border: 0px solid #010101;
        }

        &::-webkit-slider-thumb {
            box-shadow: none;
            border: 1px solid black;
            box-shadow: 0px 5px 10px rgba(0,0,0,0.25);
            height: 20px;
            width: 20px;
            border-radius: 22px;
            background: white;
            cursor: pointer;
            -webkit-appearance: none;
            margin-top: -10px;
        }
        &::-moz-range-thumb{
            box-shadow: none;
            border: 1px solid black;
            box-shadow: 0px 5px 10px rgba(0,0,0,0.25);
            height: 20px;
            width: 20px;
            border-radius: 22px;
            background: white;
            cursor: pointer;
            -webkit-appearance: none;
            margin-top: -10px;
        }
        &::-moz-focus-outer {
            border: 0;
            }
    }
    .rs-label {    
        position: relative;
        transform-origin: center center;
        display: block;
        margin-left: -1em;
        width: 2em;
        height: 2em;
        background: transparent;
        text-align: center;
        font-weight: bold;
        box-sizing: border-box;
        margin-top: 15px;
        font-style: normal;
        font-weight: normal;
        line-height: normal;
        font-size: 1em;
        .dimension{
            font-size: 0.7em;
            letter-spacing: 0.07em;
            margin-top: -2px;
        }
    }
}